.offer {
    background: url('../img/offer/offer-bg.png') no-repeat;
    background-position: center right;
    background-size: contain;
    position: relative;
    &:before {
        content: '';
        width: 50%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        background-color: #fff; }
    &-block {
        width: 50%;
        padding-right: 90px;
        position: relative; }
    &__title {
        max-width: 360px;
        line-height: 35px;
        margin-bottom: 24px; }
    &__subtitle {
        max-width: 250px;
        margin-bottom: 30px; }
    &__input {
        margin-bottom: 30px;
        width: 240px;
        &:first-child {
            margin-right: 10px; } }
    &__button {
        width: 100%;
        min-height: 70px; }

    &__subtext {
        display: flex;
        justify-content: space-between; }

    &__description {
        display: inline-block;
        width: 225px;
        margin-top: 20px;
        font-size: 15px; }


    &__tel {
        display: inline-block;
        width: 195px;
        margin-top: 20px;
        font-size: 15px; } }


@media (max-width: 1200px) {
    .offer {
        background-size: cover;
        &__input {
            width: 100%;
            margin-bottom: 10px; }
        &__button {
            font-size: 13px; } } }
@media (max-width: 768px) {
    .offer {
        background: #ffffff;
        &-block {
            width: 100%;
            padding-right: 0; } } }



